








import { Component, Prop, Vue } from 'vue-property-decorator';
import { setTimeSpeed } from '@/store/modules/timer';

@Component
export default class DebugButtons extends Vue {
    public speedCheat = 1;

    public cheat() {
        this.speedCheat *= 10;
        if (this.speedCheat > 100) {
            this.speedCheat = 1;
        }
        setTimeSpeed(this.speedCheat);
    }
}
